import React from 'react';
import useSound from "use-sound";
import meowSfx from "../sounds/meow.mp3";
import "./Card.css"

const Card = ({ name, age, id, food, fans }) => {

  const soundUrl = meowSfx;
  const [playbackRate, setPlaybackRate] = React.useState(0.70);
  const [count, setCount] = React.useState(0);

  const [play] = useSound(soundUrl, {
    playbackRate,
    volume: 0.5,
  });

  const handleClick = () => {
    setPlaybackRate(playbackRate + 0.05);
    setCount(count + 1);
    play();
  };

  return count === 15 ? <div onClick={handleClick} className="tc bg-red white dib pa4 ma3 grow shadow-5 pointer miau-card">
    <img src={`https://robohash.org/set_set4/${id}?size=200x200`} alt="kitties" />
    <div>
      <h1 className="white disable-select">MIAU</h1>
    </div>
  </div> : (
      <div onClick={handleClick} className="tc greyCard dib ma3 grow shadow-5 pointer">

        <div className="pic-background">
          <h3 className="disable-select">{name}</h3>
          <div className="ma3">
            <img className="disable-select kitty-img" src={`https://robohash.org/set_set4/${id}?size=150x150`} alt="kitties" />
          </div>
        </div>
        <div className="content">
          <div className="stats">
            <div className="age">
              <p className="stat-value">{age}</p>
              <p className="disable-select stat-title">Age</p>
            </div>
            <div className="food">
              <p className="stat-value">{food}</p>
              <p className="disable-select stat-title">Favorite Food</p>
            </div>
            <div className="fans">
              <p className="stat-value">{fans}</p>
              <p className="disable-select stat-title">Lvl</p>
            </div>
          </div>
          <p className="disable-select desc">{name} is lorem ipsum dolor sit amet consectetur adipisicing eliter. Temporiri bus dolorem dolores dore dolorum dicta, nostrum asper natur volup. Sonsectetur adipisicing ipsam.</p>
        </div>
      </div>
    );
}

export default Card;